
body {
  background-color: #e6ede9;
  font-size: 16px;
}
label {
  font-size: 0.8em;
  display: block;
  margin-bottom: 0.5em;
}
a {
  color: #1aad8d;
}
a:hover {
  color: #e3a826;
}
a:visited {
  color: #1aad8d;
}
button,
select,
option {
  border: none;
  box-shadow: none;
  background-color: #fff;
  outline: 0;
}
button:focus,
select:focus,
option:focus {
  outline: 0;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appereance: none;
  box-sizing: border-box;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;
  color: #1aad8d;
  background-image: linear-gradient(45deg, transparent 50%, #1aad8d 50%), linear-gradient(135deg, #1aad8d 50%, transparent 50%), linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), calc(100% - 2.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1px 1.5em;
  background-repeat: no-repeat;
}
button.btn {
  padding: 0.5em 2em;
  font-size: 1em;
}
button.btn,
select {
  border: #179b7e solid 1px;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  border-radius: 0.25em;
  color: #1aad8d;
  font-weight: bold;
  background-color: #fff;
}
body {
  font-family: 'Asap', sans-serif;
}
.menu-container {
  position: fixed;
  bottom: 0;
  z-index: 50;
  margin: 5em;
  width: 25em;
  max-width: 25em;
}
.menu {
  border: #0d5948 1px solid;
  border-top-width: 10px;
  border-bottom-width: 5px;
  padding: 1em 2em;
  background-color: #1aad8d;
  color: #fff;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  margin-top: 1em;
}
.menu .title {
  border: 1.5px #d3f8f0;
  border-style: dotted none;
  padding: 0.5em 0;
  letter-spacing: 0.0625em;
}
.menu .title h1,
.menu .title h2 {
  margin: 0 0 0.5em 0;
}
.menu .title h1 {
  font-size: 1.7em;
  letter-spacing: 0.0325em;
  font-family: 'Belgrano', serif;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5), 1px 1px 1px rgba(0,0,0,0.5);
}
.menu .title h2 {
  font-size: 0.8em;
  font-style: italic;
}
.links {
  margin: 1em 5em 0 0;
  position: absolute;
  list-style: none;
  top: 0;
  right: 0;
}
.icon svg {
  padding: 0.1em;
  background-color: #fff;
  border-radius: 50%;
  display: inline-block;
  width: 3em;
  height: 3em;
  fill: #1aad8d;
}
.set {
  margin: 1em;
  display: inline-block;
}
.console {
  display: block;
  max-width: 20em;
  position: fixed;
  margin: 5em;
  right: 0;
  bottom: 0;
  color: #0d5948;
}
#app {
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.dropped {
  border: #1aad8d dashed 1px;
  margin: 0.125em;
  background-color: #fff;
  cursor: pointer;
}
.dropped:hover {
  border-color: #e3a826;
}
.dropped:hover .icon svg {
  fill: #e3a826;
}
.dropped .name {
  font-size: 0.6em;
  margin: 0 0.5em 0.25em 0.5em;
}
.dropped .icon,
.dropped .icon svg {
  margin: 0;
  padding: 0;
}
.dropped .icon svg {
  fill: #1aad8d;
  background: transparent;
}
.dialog-drag {
  min-width: 10em;
  background-color: #e6eee9;
  box-shadow: 2px 2px 1px rgba(0,0,0,0.5);
}
.dialog-1.dialog-drag {
  border: #1aad8d dashed 2px;
  background-color: #fff;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}
.dialog-1.dialog-drag .dialog-header {
  background-color: transparent;
}
.dialog-1.dialog-drag .dialog-header .buttons button {
  color: #1aad8d;
}
.dialog-1.dialog-drag .dialog-header .title {
  display: none;
}
.dialog-drag.dialog-1.fixed {
  border: #1aad8d solid 2px;
}
.dialog-2.dialog-drag {
  border-color: #1aad8d;
}
.dialog-2.dialog-drag .dialog-header {
  background-color: #1aad8d;
  color: #fff;
}
.dialog-2.dialog-drag .dialog-header .buttons button {
  color: #fff;
}
.dialog-3.dialog-drag {
  border: none;
  background-color: #fdfaf1;
  -webkit-animation-name: dialog-3-anim;
          animation-name: dialog-3-anim;
}
.dialog-3.dialog-drag .dialog-header {
  background-color: #e3a826;
  color: #fff;
}
.dialog-3.dialog-drag .dialog-header .buttons button {
  color: #f9edd2;
  text-shadow: none;
}
.dialog-3.dialog-drag .dialog-header .buttons button:hover {
  color: #fff;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}
.dialog-drag.dialog-3.fixed {
  margin: 2em;
  outline: #1aad8d 2px dashed;
  outline-offset: 0.25em;
}
@-webkit-keyframes dialog-3-anim {
0% {
    opacity: 0;
    -webkit-transform: scaleX(0.1) scaleX(3);
            transform: scaleX(0.1) scaleX(3);
}
100% {
    opacity: 1;
}
}
@keyframes dialog-3-anim {
0% {
    opacity: 0;
    -webkit-transform: scaleX(0.1) scaleX(3);
            transform: scaleX(0.1) scaleX(3);
}
100% {
    opacity: 1;
}
}


